import React, { Component } from "react";

import "./App.css";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrashAlt } from "@fortawesome/free-solid-svg-icons";
import { Modal, ModalBody, ModalFooter, ModalHeader } from "reactstrap";

// perticios para hacer mijson
const url = "https://testing-pay-license.usound.co/payments/api/api/clients/";
// extracion de las peticiones del json
class App extends Component {
  state = {
    data: [],
    modalInsertar: false,
    modalinsertar: false,
    form: {
      id: "",
      timestamp: "",
      nombre: "",
      email: "",
      phone: "",
      razon_social: "",
      identifier: "",
      country: "",
      type_id: "",
      iva: "",
      type: "",
      person: "",
      vat_number: "",
      carnet_id: "",
      phone_country_code: "",
      phone_area_code: "",
      phone_work_country_code: "",
      phone_work_area_code: "",
      phone_work: "",
      tipoModal: "",
    },
  };
  // peticion GET
  peticionGet = () => {
    axios
      .get(url)
      .then((response) => {
        console.log(response.data);
        this.setState({ data: response.data });
      })
      .catch((error) => {
        console.log(error.message);
      });
  };
  // PETICION POST
  peticionPost = async () => {
    delete this.state.form.id;
    await axios
      .post(url, this.state.form)
      .then((response) => {
        this.modalInsertar();
        this.peticionGet();
      })
      .catch((error) => {
        console.log(error.message);
      });
  };
  // peticion PUT
  peticionPut = () => {
    axios.put(url + this.state.form.id, this.state.form.id).then((response) => {
      this.modalInsertar();
      this.peticionGet();
    });
  };
  // Peticion Delete
  peticionDelete = () => {
    axios.delete(url + this.state.form.id).then((response) => {
      this.setState({ modalEliminar: false });
      this.peticionGet();
    });
  };
  // insertar
  modalInsertar = () => {
    this.setState({ modalInsertar: !this.state.modalInsertar });
  };
  // nos dirigimos a sus datos exactos para interactuar
  seleccionarEmpresa = (empresa) => {
    this.setState({
      tipoModal: "actualizar",
      form: {
        id: empresa.id,
        timestamp: empresa.timestamp,
        nombre: empresa.nombre,
        email: empresa.email,
        phone: empresa.phone,
        razon_social: empresa.razon_social,
        identifier: empresa.identifier,
        country: empresa.country,
        type_id: empresa.type_id,
        iva: empresa.iva,
        type: empresa.type,
        person: empresa.person,
        vat_number: empresa.vat_number,
        carnet_id: empresa.carnet_id,
        phone_country_code: empresa.phone_country_code,
        phone_area_code: empresa.phone_area_code,
        phone_work_country_code: empresa.phone_work_country_code,
        phone_work_area_code: empresa.phone_work_area_code,
        phone_work: empresa.phone_work,
      },
    });
  };
  // copia de datos
  handleChange = async (e) => {
    e.persist();
    await this.setState({
      form: {
        ...this.state.form,
        [e.target.name]: e.target.value,
      },
    });
    console.log(this.state.form);
  };

  componentDidMount() {
    this.peticionGet();
  }

  // agrego formulario
  render() {
    const { form } = this.state;

    return (
      <div className="App">
        <br />
        <button
          className="btn btn-success"
          onClick={() => {
            this.setState({ form: null, tipoModal: "insertar" });
            this.modalInsertar();
          }}
        >
          Agregar
        </button>
        <br />
        <br />
        <table className="table ">
          <thead>
            <tr>
              <th>#</th>
              <th>Fecha</th>
              <th>Nombre</th>
              <th>correo</th>
              <th>Phone</th>
              <th>Solucion</th>
            </tr>
          </thead>
          <tbody>
            {this.state.data.map((empresa) => {
              return (
                <tr>
                  <td>{empresa.id}</td>
                  <td>{empresa.timestamp}</td>
                  <td>{empresa.nombre}</td>
                  <td>{empresa.email}</td>
                  <td>{empresa.phone}</td>
                  <td>{empresa.razon_social}</td>

                  <td>
                    <button
                      className="btn btn-primary"
                      onClick={() => {
                        this.seleccionarEmpresa(empresa);
                        this.modalInsertar();
                      }}
                    >
                      {/* //edit y eliminar iconos  */}
                      <FontAwesomeIcon icon={faEdit} />
                    </button>
                    {""}
                    <button
                      className="btn btn-danger"
                      onClick={() => {
                        this.seleccionarEmpresa(empresa);
                        this.setState({ modalEliminar: true });
                      }}
                    >
                      <FontAwesomeIcon icon={faTrashAlt} />
                    </button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
        {/* // forumario para agregar  */}
        <Modal isOpen={this.state.modalInsertar}>
          <ModalHeader style={{ display: "block" }}>
            <span
              style={{ float: "right" }}
              onClick={() => this.modalInsertar()}
            >
              x
            </span>
          </ModalHeader>
          <ModalBody>
            <div className="form-group">
              <label htmlFor="id">#</label>
              <input
                className="form-control"
                type="text"
                name="id"
                id="id"
                readOnly
                onChange={this.handleChange}
                value={form ? form.id : this.state.data.length + 1}
              />
              <br />
              <div class="form-group">
                <label htmlFor="type_id">Tipo de Documento2</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="type_id"
                    id="type_id"
                    onChange={this.handleChange}
                    value={form ? form.type_id : ""}
                  >
                    <option value="" disabled selected>
                      --------
                    </option>

                    <option value="DNI">DNI</option>

                    <option value="CUIT">CUIT</option>

                    <option value="CUIL">CUIL</option>

                    <option value="DU">DU</option>
                  </select>
                </div>
              </div>
              <br />
              <label htmlFor="identifier">Num de documento</label>
              <input
                className="form-control"
                type="text"
                name="identifier"
                id="identifier"
                onChange={this.handleChange}
                value={form ? form.identifier : ""}
              />

              <br />
              <div class="form-group">
                <label htmlFor="person">Persona</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="person"
                    id="person"
                    onChange={this.handleChange}
                    value={form ? form.person : ""}
                  >
                    <option value="empty">empty</option>

                    <option value="fisica">fisica</option>
                    <option value="juridica">juridica</option>
                  </select>
                </div>
              </div>
              <br />
              <label htmlFor="nombre">Nombre y Apellido</label>
              <input
                className="form-control"
                type="text"
                name="nombre"
                id="nombre"
                onChange={this.handleChange}
                value={form ? form.nombre : ""}
              />
              <br />
              <br />
              <label htmlFor="razon_social">Razon Social</label>
              <input
                className="form-control"
                type="text"
                name="razon_social"
                id="razon_social"
                onChange={this.handleChange}
                value={form ? form.razon_social : ""}
              />
              <br />
              <br />
              <label htmlFor="vat_number">Cuit/Cuil/Rut</label>
              <input
                className="form-control"
                type="Number"
                name="vat_number"
                id="vat_number"
                onChange={this.handleChange}
                value={form ? form.vat_number : ""}
              />
              <br />
              <div class="form-group">
                <label htmlFor="iva">Condici√≥n Fiscal</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="iva"
                    id="iva"
                    onChange={this.handleChange}
                    value={form ? form.iva : ""}
                  >
                    <option value="empty">empty</option>

                    <option value="consumidor final">consumidor final</option>

                    <option value="responsable inscripto">
                      responsable inscripto
                    </option>

                    <option value="exento">exento</option>

                    <option value="monotributista">monotributista</option>
                  </select>
                </div>
              </div>
              <br />
              <div class="form-group">
                <label htmlFor="country">PAIS</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    className="form-control"
                    type="text"
                    name="country"
                    id="country"
                    onChange={this.handleChange}
                    value={form ? form.country : ""}
                  >
                    <option value="AR">AR</option>

                    <option value="CO">CO</option>
                  </select>
                </div>
              </div>
              <br />
              <label htmlFor="carnet_id">Num. de Matricula</label>
              <input
                className="form-control"
                type="Number"
                name="carnet_id"
                id="carnet_id"
                onChange={this.handleChange}
                value={form ? form.carnet_id : ""}
              />
              <br />
              <br />
              <div class="form-group">
                <label htmlFor="type">Tipo de Cliente</label>

                <div class="col-sm-10">
                  <select
                    class="form-control"
                    name="type"
                    className="form-control"
                    type="text"
                    id="type"
                    onChange={this.handleChange}
                    value={form ? form.type : ""}
                  >
                    <option value="Cliente Final">Cliente Final</option>

                    <option value="Distribuidor">Distribuidor</option>
                  </select>
                </div>
              </div>
              <br />
              <label>
                ______________________________________________________________________
              </label>
              <br />
              <label htmlFor="email">Correo</label>
              <input
                className="form-control"
                type="email"
                name="email"
                id="email"
                onChange={this.handleChange}
                value={form ? form.email : ""}
              />
              <br />
              <label htmlFor="phone_country_code">Cod . Pais</label>
              <input
                className="form-control"
                type="Number"
                name="phone_country_code"
                id="phone_country_code"
                onChange={this.handleChange}
                value={form ? form.phone_country_code : ""}
              />
              <br />
              <br />
              <label htmlFor="phone_area_code">Cod area</label>
              <input
                className="form-control"
                type="Number"
                name="phone_area_code"
                id="phone_area_code"
                onChange={this.handleChange}
                value={form ? form.phone_area_code : ""}
              />
              <br />
              <br />
              <label htmlFor="phone">Tel. Personal</label>
              <input
                className="form-control"
                type="Number"
                name="phone"
                id="phone"
                onChange={this.handleChange}
                value={form ? form.phone : ""}
              />
              <br />
              <br />
              <label htmlFor="phone_work_country_code">Cod Pais</label>
              <input
                className="form-control"
                type="Number"
                name="phone_work_country_code"
                id="phone_work_country_code"
                onChange={this.handleChange}
                value={form ? form.phone_work_country_code : ""}
              />
              <br />
              <br />
              <label htmlFor="phone_work_area_code">Cod area</label>
              <input
                className="form-control"
                type="Number"
                name="phone_work_area_code"
                id="phone_work_area_code"
                onChange={this.handleChange}
                value={form ? form.phone_work_area_code : ""}
              />
              <br />
              <br />
              <label htmlFor="phone_work">Tel.Laboral</label>
              <input
                className="form-control"
                type="Number"
                name="phone_work"
                id="phone_work"
                onChange={this.handleChange}
                value={form ? form.phone_work : ""}
              />
              <br />

              <br />
            </div>
          </ModalBody>
          {/* //botones para  el formulario  */}
          <ModalFooter>
            {this.state.tipoModal || "insertar" ? (
              <button
                className="btn btn-success"
                onClick={() => this.peticionPost()}
              >
                Insertar
              </button>
            ) : (
              <button
                className="btn btn-primary"
                onClick={() => this.peticionPut()}
              >
                {" "}
                Actualizar
              </button>
            )}
            <button
              className="btn btn-danger"
              onClick={() => this.modalInsertar()}
            >
              Cancelar
            </button>
          </ModalFooter>
        </Modal>
        <Modal isOpen={this.state.modalEliminar}>
          <ModalBody>estas seguro de eliminar {form && form.nombre}</ModalBody>
          <ModalFooter>
            <button
              className="btn btn-danger"
              onClick={() => this.peticionDelete()}
            >
              Si
            </button>
            <button
              className="btn btn-secundary"
              onClick={() => this.setState({ modalEliminar: false })}
            >
              no
            </button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default App;
